<template>
  <div id="container" ref="container">
    <div class="icon-container" v-if="showIcon">
      <i class="el-icon-loading"></i>
    </div>
    <div v-if="showCharts" class="main" id="numMonthContainer"></div>
  </div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      showCharts: false,
      showIcon: false,
      containerWidth: 0,
      containerHeight: 0
    };
  },
  props: {
    requestFunc: [Function, Boolean]
  },
  mounted() {
    this._initDom();
    this.getStatisticsNumberHandler();
  },
  methods: {
    getStatisticsNumberHandler() {
      this.requestFunc().then(res => {
        console.log(res);
        if (res.code !== 0) {
          return false;
        }
        this.showCharts = true;
        this.showIcon = false;
        this.$nextTick().then(v => {
          this._initOptions(res.data);
        });
      });
    },
    _initDom() {
      this.container = this.$refs.container;
      this.container.style.width = "100%";
      this.container.style.height = "400px";
      this.showIcon = true;
    },
    _initOptions(data) {
      let date = new Date().getDate();
      let xs = data.map(v => v.x);
      let dataArr = [];
      for (let i = 1; i <= date; i++) {
        if (xs.includes(i)) {
          dataArr.push([i, data.find(x => x.x == i).y]);
          continue;
        }
        dataArr.push([i, 0]);
      }
      var dom = this.$refs.container;
      var myChart = echarts.init(dom);
      let option = {
        title: { text: "", left: "center" },
        xAxis: {
          type: "value",
          min: 0,
          max: date,
          splitLine: {
            show: false
          },
          maxInterval: 3600 * 24 * 1000
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: false
          }
        },
        series: [
          {
            data: dataArr,
            type: "bar",
            label: {
              normal: { show: true }
            }
          }
        ]
      };
      if (option && typeof option === "object") {
        myChart.setOption(option, true);
      }
    }
  }
};
</script>
<style lang="css" scoped>
#container {
  position: relative;
  width: 100%;
  height: 100%;
}
.main {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.icon-container {
  z-index: 2;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -12px;
  margin-top: -12px;
}
</style>
